:root{
    --bg-color: #ffffff;
    --bg-color-hover: #fafafa;
    --border-color: #e7e7e7;
    --text-color:#666666;
    --text-color-ready:#cccccc ;
    --checkbox-color:#76cbbb;
    }


.todos-list {
    margin: 0;
    border: none;
    min-height: 300px;
    position: relative;
    font-weight: 100;

    .todo-item {
        height: 60px;
        position: relative;
        border-top: 1px solid var(--border-color);
        border-bottom: 1px solid var(--border-color);
        border-left: none;
        border-right: none;
        margin-bottom: -1px;
        padding: 0 20px;

        flex-direction: row;
        align-items: center;
        display: flex;

        &.__ready {
            .todo-item_textbox {
                .todo-item_text {
                    text-decoration: line-through;
                    color: var(--text-color-ready);
                    }
                }
            }
        &:hover {
            background: var(--bg-color-hover);
            cursor: pointer;
            }
        &:hover .todo-item_remove {
            background: url('../../../images/icons/close.png') no-repeat 0 0;
            }

        &:hover .todo-item_text {
            background-color: var(--bg-color-hover);
            }

        .todo-item_checkbox {

            width: 36px;
            height: 36px;
            border: 1px solid var(--border-color);
            border-radius: 50px;
            margin-right: 12px;
            flex-basis: 36px;

            .todo-item_checkbox-layers {
                width: 36px;
                height: 36px;
                position: absolute;
                z-index: 2;

                .todo-item_ready-mark {

                    opacity: 0;
                    padding: 0;
                    width: 36px;
                    height: 36px;
                    overflow: hidden;
                    position: absolute;
                    z-index: 0;

                    &:checked + .todo-item_ready-img {
                        background: url('../../../images/icons/completed.png') no-repeat center;
                        border: 1px solid var(--checkbox-color);
                        }
                    }
                .todo-item_ready-img {
                    padding: 0;
                    width: 36px;
                    height: 36px;
                    border-radius: 50px;
                    position: absolute;
                    z-index: 1;
                    }
                }

            }
        .todo-item_textbox {
            overflow: hidden;
            flex-grow: 2;
            padding-top: 24px;

            .todo-item_text {
                width: 100%;

                padding-right: 32px;
                resize: none;
                border: none;
                outline: none;
                font-size: 20px;
                color: var(--text-color);
                }
            }

        .todo-item_remove {
            flex-basis: 16px;
            width: 16px;
            height: 16px;
            position: absolute;
            bottom: 22px;
            right: 12px;
            border: 0;

            background: var(--bg-color);
            outline: none;
            }
        }
    }
